body {
  font-family: cursive; 
  color: #333; }

.navbar {
  z-index: 2;
  border-bottom: #008ed6 3px solid;
  opacity: 0.8; }

.navbar li {
  font-size: 18px;
}

#home-section {
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  color: #fff;
  min-height: 700px; }
  #home-section .dark-overlay {
    background-color: rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 700px; }
  #home-section .home-inner {
    padding-top: 220px; } 
 
.section-padding {
    padding-top: 70px;
  }

.header-image {    
  position: relative;
  z-index: 3 !important;
  width: 150px; 
  height: 150px;
  border-radius: 50%;
  border-width: 4px;
  border-style: solid;
  border-color: #fff;
  margin-top: -100px; 
}

#home .header-image {
  border-color: #17a2b8;
}
 
.login {
  position: relative;
  width: 50%;
  height: auto;
  margin: auto;
  padding-top: 100px;
  padding-bottom: 100px;
}

.login img {
  width: 150px;
  height: 150px;
  border: #17a2b8;
  border-radius: 50%;
  border-style: solid;
}

#main-form {
  width: 100%;
  margin: auto;
}

#contact .card p {
  width: 100%;
  margin: auto;
  padding-bottom: 20px;
}

@media screen and (min-width: 480px) {
  #main-form {
    width: 60%;
    margin: auto;
  }
  
  #contact .card p {
    width:80%;
    margin: auto;
    padding: 20px;
  }
}


